html {
    box-sizing: border-box;
    font-size: 15px;
    padding: 0;
    margin: 0;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    color: $black;
    font-family: $font-family;
    line-height: 1.3;
    margin: 0 auto;
    width: 80%;
    background-image: url("../img/bg-pattern-top-mobile.svg"), url("../img/bg-pattern-bottom-mobile.svg");
    background-repeat: no-repeat, no-repeat;
    background-position: left top, right bottom;
    height: 100vh;
}

main {
    text-align: center;
}

//Top Section
.top-section {
    .header {
        h1 {
            font-weight: 700;
            color: $veryDarkMagenta;
        }
        p {
            font-weight: 500;
        }
    }

    .rating-container {
        .ratings {
            background-color: $ltMagenta;
            padding: 0.66rem;
            margin-bottom: 0.33rem;
            margin-top: 1rem;
            border-radius: 0.33rem;

            .fa {
                color: $yellow;
                padding: .27rem;
            }


            p {
                font-weight: 700;
                display: inline;
                font-size: 0.8rem;
                padding: 0.33rem;
                color: $veryDarkMagenta;
            }
        }
    }
}

//Main Content
.main-content {
    margin: 1.33rem auto;
    display: inline-block;

    .card {
        background-color: $veryDarkMagenta;
        color: $white;
        padding: 1.33rem;
        margin-bottom: 0.8rem;
        text-align: left;
        border-radius: 5px;

        .card-header {
            display: flex;
            align-items: center;
            justify-content: flex-start;

            img {
                border-radius: 50%;
                width: 3rem;
            }
            .caption {
                padding-top: 15px;
                padding-left: 10px;

                h2 {
                    display: inline;
                    padding: 5px;
                }

                span {
                    color: $softPink;
                    display: block;
                    padding: 5px;
                }
            }
        }

        .quote {
            margin-top: 1.33rem;
            margin-bottom: 1.33rem;
        }
    }
}

.attribution {
    font-size: 11px;
    text-align: center;
}
.attribution a {
    color: hsl(228, 45%, 44%);
}

//Media Queries

@media only screen and (min-width: 620px) {
    body {
        background-image: url("../img/bg-pattern-top-desktop.svg"), url("../img/bg-pattern-bottom-desktop.svg");
        background-repeat: no-repeat, no-repeat;
        background-position: left top, right bottom;
        background-size: 30%,70%;
        padding: 1.33rem;
    }

    .top-section {
        display: flex;
        justify-content: space-around;

        .header {
            flex: 1;
            text-align: left;
            padding: 6.67rem 0;
            
            h1 {    
                font-size: 3em;
                width: 70%;
            }

            p{
                width: 70%;
            }

            
        }

        .rating-container {
           //flex-basis: 40%;
            padding-left: 2rem;
            padding-top: 6.67rem;

            .ratings {
                padding: 0.3rem 1.5rem;
                display: flex;
                align-items: center;

                .stars {
                    padding-right: 1.3rem;
                    display: inline-flex;
                }

                p {
                    display: inline-block;
                }

                &:first-child {
                    transform: translateX(-20px);
                }

                &:last-child {
                    transform: translateX(20px);
                }
            }
        }
    }

    .main-content {
        display: flex;
        justify-content: space-around;

        .card {
            flex: 1;
            margin-right: 2rem;
            width: 100%;
            &:first-child {
                transform: translateY(-20px);
            }
            &:last-child {
                transform: translateY(20px);
                margin-right: 0;
            }
        }
    }
}

@media only screen and (width: 280px) {
    html {
        font-size: 10px;
    }
    body {
        background-image: url("../img/bg-pattern-top-mobile.svg"), url("../img/bg-pattern-bottom-mobile.svg");
        background-repeat: no-repeat, no-repeat;
        background-position: left top, right bottom;
    }
}

@media only screen and(width:1024px) {
    body {
        width: 100%;
    }

    .header {
        text-align: left;
        //padding: 6.67rem;

        h1 {
            font-size: 2.4em;
        }

        p {
            font-size: 1.4em;
        }
    }
}

@media only screen and(width:768px) {
    body {
        width: 100%;
    }

    .header {
        text-align: left;
        padding: 0;

        h1 {
            font-size: 1.4em;
        }

        p {
            font-size: 1em;
        }
    }

    .rating-container {
        padding: 0;
    }
}
